@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700,800);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.p-actors {
  margin: 0 auto;
}
.p-actors > .title {
  text-align: center;
  padding-bottom: 15px;
  color: darkgoldenrod;
  font-size: 48px !important;
}

.p-actors .input-filter{
}
.p-actors > .sortBy {
  display: -webkit-flex;
  display: flex;
  padding-top: 10px;
  color: white;
}
.p-actors > .sortBy > span {
  font-size: 17px;
  font-weight: bold;
}
.p-actors > .sortBy > .mb-3 {
  display: -webkit-flex;
  display: flex;
}

.p-actors > .sortBy > .mb-3 > .radioOpt {
  margin-left: 20px;
}

.p-actors .actorCard {
  margin: 30px;
}
.p-actors .actorCard a {
  color: rgb(248, 186, 31);
  font-weight: bold;
}
.p-actors .actorCard .card-body {
  text-align: center;
}
.spinner {
  margin-left: 50%;
}

.p-actors .actorCard:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  box-shadow: 0px 0px 80px -25px rgba(0, 0, 0, 0.5);
  transition: all 0.4s;
}

.c-searchBox{
    position: relative;
}

.c-searchBox > .result-box{
    position: absolute;
    width: 100%;
    height: 300px !important;
    overflow-y: auto;
    z-index: 100;
}
* {
  box-sizing: border-box;
  margin: 0;
}

.link {
  display: block;
  text-align: center;
  color: #777;
  text-decoration: none;
  padding: 10px;
}
.movie_card {
  position: relative;
  width: 540px;
  height: 350px;
  margin: 40px auto;
  overflow: hidden;
  border-radius: 10px;
  transition: all 0.4s;
  box-shadow: 0px 0px 120px -25px rgba(0, 0, 0, 0.5);
}
.movie_card:hover {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  box-shadow: 0px 0px 80px -25px rgba(0, 0, 0, 0.5);
  transition: all 0.4s;
}

.info_section {
  position: relative;
  width: 100%;
  height: 100%;
  background-blend-mode: multiply;
  z-index: 2;
  border-radius: 10px;
}

.movie_header {
  position: relative;
  padding: 25px;
  height: 40%;
}
h1 {
  color: black;
  font-size: 18px !important;
}
h4 {
  color: #555;
  font-size: 14px !important;
}
.minutes {
  display: inline-block;
  margin-top: 15px;
  color: #555;
  padding: 5px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.05);
}
.type {
  display: inline-block;
  color: #959595;
  margin-left: 10px;
}
.locandina {
  position: relative;
  float: left;
  margin-right: 20px;
  height: 120px;
  box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.5);
}
.movie_desc {
  padding: 25px;
  height: 50%;
}
.text {
  color: #545454;
  height: 150px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.movie_social {
  height: 10%;
  padding-left: 15px;
  padding-bottom: 20px;
}
ul {
  list-style: none;
  padding: 0;
}
li {
  display: inline-block;
  /* color: rgba(14, 13, 13, 0.3); */
  color: darkgoldenrod;
  font-weight: bold;
  transition: color 0.3s;
  transition-delay: 0.15s;
  margin: 0 10px;
  cursor: pointer;
}
li:hover {
  transition: color 0.3s;
  color: rgba(0, 0, 0, 0.7);
}
.blur_back {
  position: absolute;
  top: 0;
  z-index: 1;
  height: 100%;
  right: 0;
  background-size: cover;
  border-radius: 11px;
}

@media screen and (min-width: 768px) {
  /* .movie_header {
    width: 65%;
  } */

  .movie_desc {
    width: 50%;
  }

  .info_section {
    background: linear-gradient(to right, #e5e6e6 50%, transparent 100%);
  }

  .blur_back {
    width: 80%;
    background-position: -100% 10% !important;
  }
}

@media screen and (max-width: 768px) {
  .movie_card {
    width: 95%;
    margin: 70px auto;
    min-height: 350px;
    height: auto;
  }

  .blur_back {
    width: 100%;
    background-position: 50% 50% !important;
  }

  .movie_header {
    width: 100%;
    margin-top: 85px;
  }
  .info_section {
    background: linear-gradient(to top, #e5e6e6 50%, transparent 100%);
    display: inline-grid;
  }
}

.p-movies {
    margin-top: 10px;
}

.p-movies .bold-title{
    font-weight: bold;
}

.p-home h1{
    color: white;
    font-size: 40px !important;
    width: 500px;
    margin: 0 auto;
}
